import React, { useContext } from 'react';
import { FaMusic, FaBox, FaList, FaUserFriends } from 'react-icons/fa';
import { GiCompactDisc } from 'react-icons/gi';
import { FiYoutube } from 'react-icons/fi';
import { BsMusicNoteList, BsBarChart, } from 'react-icons/bs';
import { RiRemoteControl2Line } from 'react-icons/ri';
import './NavCpn.scss';
import { AuthContext } from "../../contexts/AuthContext";
import Profile from "../Profile";

function Nav(props) {
    const { isAuthenticated } = useContext(AuthContext);

    return (
        <div className={`nav-container ${isAuthenticated && "open"}`}>
            <nav className="nav-Left">
                <span className="discover">Khám phá</span>
                <ul>
                    <li>
                        <a href="/" className="link">
                            <span className="img" style={{ color: "#1ab667" }}>
                                <GiCompactDisc />
                            </span>
                            <span className="text">What's New</span>
                        </a>
                    </li>
                    <li>
                        <a href="/" className="link">
                            <span className="img" style={{ color: "#4cb6cb" }}>
                                <FaMusic />
                            </span>
                            <span className="text">Genres</span>
                        </a>
                    </li>
                    <li>
                        <a href="/" className="link">
                            <span className="img" style={{ color: "#e8eff0" }}>
                                <FaBox />
                            </span>
                            <span className="text">Events</span>
                        </a>
                    </li>
                    <li>
                        <a href="/" className="link">
                            <span className="img" style={{ color: "#788188" }}>
                                <FaList />
                            </span>
                            <span className="text">Listen</span>
                        </a>
                    </li>
                    <li>
                        <a href="/" className="link">
                            <span className="img" style={{ color: "#f05050" }}>
                                <FiYoutube />
                            </span>
                            <span className="text">Youtube</span>
                        </a>
                    </li>
                </ul>

                <div className="list-control__container">
                    <span className="discover last">Điều Hướng</span>
                    <ul className="list-control">
                        <li>
                            <a href="/" className="item-control">
                                <span className="icon"><BsMusicNoteList /></span>
                                <span className="control-text">Nhạc Mới</span>
                            </a>
                        </li>
                        <li>
                            <a href="#top-songs" className="item-control">
                                <span className="icon"><BsBarChart /></span>
                                <span className="control-text">Top Nhạc Hay</span>
                            </a>
                        </li>
                        <li>
                            <a href="#control" className="item-control">
                                <span className="icon"><RiRemoteControl2Line /></span>
                                <span className="control-text">Điều khiển Nhạc</span>
                            </a>
                        </li>
                        <li>
                            <a href="#friends" className="item-control">
                                <span className="icon"><FaUserFriends /></span>
                                <span className="control-text">Bạn bè</span>
                            </a>
                        </li>
                    </ul>
                </div>

            </nav >
            <Profile location={"bottom"} />
        </div>
    );
}

export default Nav;