import React, { useContext } from "react";
import './Header.scss'
import { AiOutlineSearch, AiOutlineMenuUnfold, AiOutlineMenuFold } from "react-icons/ai";
import Notification from "../Notification"
import Profile from "../Profile";
import { AuthContext } from "../../contexts/AuthContext";


function Header(props) {
    const { isAuthenticated, toggleAuth } = useContext(AuthContext);

    return (
        <header>
            <div className="header">
                <button className={isAuthenticated ? "home open" : "home"}>
                    <img src={process.env.PUBLIC_URL + '/images/avatar/haha.png'} alt="" />
                    <span>Panda Zing</span></button>
                <button
                    className="toggle-header"
                    onClick={toggleAuth}
                >
                    {isAuthenticated ? <AiOutlineMenuFold /> : <AiOutlineMenuUnfold />}
                </button>
                <form className="search">
                    <button className="button"><AiOutlineSearch /></button>
                    <input className="input-search" type="input" placeholder="Tìm Kiếm nhạc, ablum" />
                </form>
                <div className="left">
                    <Notification />
                    <Profile location={"top"} />
                </div>
            </div>
        </header>
    );
}

export default Header;