import React from 'react';
import {
    FaPlay,

} from 'react-icons/fa';
import { RiCloseLine } from 'react-icons/ri';
import { AiFillPlayCircle } from 'react-icons/ai';

function MyList(props) {
    const {
        value,
        currentSongIndex,
        setCurrentSongIndex,
        isPlaying,
        setIsPlaying,
        myList,
        setMyList
    } = props;

    //Xử lý khi play bài hát
    function handlePlaySong(value) {
        if (isPlaying && value.id === currentSongIndex) {
            setIsPlaying(false)
        } else {
            setIsPlaying(true)
            setCurrentSongIndex(value.id)
        }
    }
    //Xoa bài hát khỏi myList
    function handleDetelemyList(id) {
        const newMyList = myList.filter(item => item.id !== id)
        localStorage.setItem('MYLIST', JSON.stringify(newMyList));
        setMyList(newMyList)
    }
    return (
        <li>
            <div className="playerSong">
                <span
                    className="btnMyPlay"
                    onClick={() => handlePlaySong(value)}
                >
                    {value.id === currentSongIndex && isPlaying ?
                        <span className="play">
                            <AiFillPlayCircle />
                        </span>
                        :
                        <span className="pause">
                            <FaPlay />
                        </span>
                    }
                </span>
                <span
                    className="song-name"
                    onClick={() => handlePlaySong(value)}
                ><strong>{value.name}</strong> by {value.singer}</span>
                <span
                    className="remove"
                    onClick={() => handleDetelemyList(value.id)}
                >
                    <span>
                        <RiCloseLine />
                    </span>
                </span>
            </div>
        </li>
    );
}

export default MyList;